export default {
  name: 'OrderData',
  data (){
    return {
      checked: false,
      option: {
        label: '',
        value: '0',
        disabled: false
      }
    }
  },
  props: {
    list: Array,
  },
  methods: {
    //查看物流
    lookWl(title,send){
      this.$createDialog({
        type: 'alert',
        title: '物流信息',
        content: '<p style="line-height:2;">快递公司：'+title+'</p>'+'<p style="line-height:2;">快递单号：'+send+'</p>',
        icon: ''
      }).show()
    },
    confirmClick(){
      this.$emit('changeCallcelOrder',true)
    }
  },
  mounted (){

  }
}



