export default {
  name: 'orderNav',
  data (){
    return {
      showSlider: false,
      selectedLabelSlots: '售后申请',
      tabs: [{
        label: '售后申请',
        icon: 'cubeic-home'
      }, {
        label: '处理中',
        icon: 'cubeic-like'
      }, {
        label: '申请记录',
        icon: 'cubeic-vip'
      }, {
        label: '',
        icon: 'cubeic-person'
      }]
    }
  },
  methods: {
    clickHandler (label) {
      // if you clicked home tab, then print 'Home'
      this.$emit('changeClick',label)
    }
  },
  mounted (){

  }
}



