  import CubePage from '../../components/cube-page.vue'
  import orderNav from './components/nav.vue'
  import orderData from './components/data.vue'
  import dataCenter from './components/dataCenter.vue'
  import dataOver from './components/dataOver.vue'
  import cancelOrder from './components/reason.vue'
  export default {
    name: 'OrderIndex',
    data (){
      return {
        selected: false,
        maskShow: false,
        scrollEvents: ['scroll'],
        scrollY: 0,
        list: [],
        curentTab: 'orderData',
        toastTime:null,
        isData: false
      }
    },
    components: {
      orderNav,
      orderData,
      CubePage,
      cancelOrder,
      dataCenter,
      dataOver
    },
    methods: {
      showToastTime() {
        this.toastTime = this.$createToast({
          time: 0,
          txt: '加载中...'
        })
        this.toastTime.show()
      },
      changeClick(val){
        // 显示
        this.$vux.loading.show({
          text: 'Loading'
        })
        if(val=='售后申请'){
          this.getData()
          this.curentTab = 'orderData'
        }else if(val=='处理中'){
          this.curentTab= 'dataCenter'
          this.getAfterlog(1)
        }else{
          this.curentTab= 'dataOver'
          this.getAfterlog(2)
        }
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      getAfterlog(type){
        this.list = []
        this.http.get(this.ports.refund.afterlog+'?type='+type, res =>{
          console.log(res)
          this.$store.commit('changeLoading',false)
          if(this.$vux.loading.isVisible()){
            // 隐藏
            this.$vux.loading.hide()
          }
          if(res.success){
            let data = res.data
            this.list = data.res
            this.isData = false
          }else{
            this.list = []
            this.isData = true
//            this.showToastTxtOnly(res.msg)
          }
        })
      },
      getData(){
        this.list = []
        this.http.get(this.ports.refund.afterindex, res =>{
          this.$store.commit('changeLoading',false)
          if(this.$vux.loading.isVisible()){
            // 隐藏
            this.$vux.loading.hide()
          }
          console.log(res)
          if(res.success){
            let data = res.data
            this.list = data.res
            this.isData = false
          }else{
            this.list = []
            this.isData = true
//            this.showToastTxtOnly(res.msg)
          }
        })
      },
      confirmClick(val){
        const that = this
        this.maskShow = true
        setTimeout( ()=>{
          that.selected = true
        },200)
      },
      changeSelected(val){
        this.selected = val
        const that = this
        if(!this.selected){
          setTimeout( ()=>{
            that.maskShow = false
          },300)
        }
      },
      changeDeleteOrder(val){
        alert(val)
        this.changeSelected(false)

      },
      scrollHandler({ y }) {
        this.scrollY = -y
      }
    },
    mounted (){
      this.getData()
    }
  }



